  height: 32px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
}

.icon-menu-open {
  fill: var(--background-color);
}

.mob-menu {
  z-index: 1;
  width: 100vw;
  height: 100vh;
  background-color: var(--background-color);
  padding-top: 40px;
  padding-bottom: 40px;
  position: fixed;
  top: 0;
  box-shadow: 0 2px 1px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 1px 6px rgba(46, 47, 66, .08);
}

.mob-menu.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.container-mob-menu {
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
  margin: 0 auto;
  padding: 0 40px;
  display: flex;
  overflow: auto;
}

.menu-btn-close {
  width: 32px;
  height: 32px;
  background: var(--background-color);
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin-bottom: 16px;
  margin-left: auto;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), fill .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.menu-btn-close:hover, .menu-btn-close:focus {
  background-color: var(--brand-color);
  fill: var(--background-color);
}

.mob-menu-item:not(:last-child) {
  margin-bottom: 30px;
}

.mob-menu-link {
  letter-spacing: .02em;
  text-transform: capitalize;
  color: var(--text-color);
  font-family: Poppins, sans-serif;
  font-size: 36px;
  font-weight: 700;
  line-height: 1.11;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.mob-menu-link:hover, .mob-menu-link:focus {
  color: var(--brand-color);
}

.mob-menu-list-soc {
  gap: 20px;
  display: flex;
}

.mob-menu-facebook {
  fill: var(--text-color);
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

.mob-menu-facebook:hover, .mob-menu-facebook:focus {
  fill: var(--brand-color);
}

.mob-menu-instagram {
  fill: var(--text-color);
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

.mob-menu-instagram:hover, .mob-menu-instagram:focus {
  fill: var(--brand-color);
}

@media screen and (min-width: 480px) {
  .header {
    padding: 18px;
  }

  .mob-menu-list-soc {
    gap: 40px;
  }
}

